body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --color-primary: #2ecc71;
  --color-secondary: #27ae60;
  --color-tertiary: #0f5e2f;
  --color-background-light: #f3f3f3;
  --color-background: #cccccc;
  --color-dev: rgb(211, 211, 211);
  --color-text: #020202;
  --color-text-light: #f8f8f8;
  --menu-item-height: 3rem;
  --color-alert: rgb(238, 45, 45);
  --color-shadow: rgb(85, 85, 85);
}

* {
  padding: 0;
  margin: 0;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1; 
  box-shadow: inset 0px 0px 5px #333;
}

::-webkit-scrollbar-thumb {
  -webkit-transition-duration: 1s;
          transition-duration: 1s;
  background: #888; 
  box-shadow: inset 0px 0px 5px rgb(78, 78, 78);
}

::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.ripple{
  position: relative;
  overflow: hidden;
}

.ripple:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 5px;
  height: 5px;
  background: rgba(255, 255, 255, 0.295);
  opacity: 0;
  border-radius: 100%;
  -webkit-transform: scale(1, 1) translate(-50%);
          transform: scale(1, 1) translate(-50%);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}

@-webkit-keyframes ripple {
  0% {
    -webkit-transform: scale(0, 0);
            transform: scale(0, 0);
    opacity: 1;
  }
  20% {
    -webkit-transform: scale(25, 25);
            transform: scale(25, 25);
    opacity: 1;
  }
  80% {
    opacity: 0;
    -webkit-transform: scale(60, 60);
            transform: scale(60, 60);
  }
}

@keyframes ripple {
  0% {
    -webkit-transform: scale(0, 0);
            transform: scale(0, 0);
    opacity: 1;
  }
  20% {
    -webkit-transform: scale(25, 25);
            transform: scale(25, 25);
    opacity: 1;
  }
  80% {
    opacity: 0;
    -webkit-transform: scale(60, 60);
            transform: scale(60, 60);
  }
}

.ripple:focus:not(:active)::after {
  -webkit-animation: ripple 1s ease-out;
          animation: ripple 1s ease-out;
}

/* App */
.app {
  display: flex;
  flex-direction: row-reverse;
}

.main {
  flex-basis: calc(100% - 220px);
}

/* Header */
.header {
  width: 100%;
  height: 7rem;
}

.header-top {
  height: 1.3rem;
  background: #cccccc;
  background: var(--color-background);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-top-content {
  line-height: 100%;
  font-size: 0.9rem;
  height: calc(100% - 5px);
  background: rgb(211, 211, 211);
  background: var(--color-dev);
}

.header-mast {
  height: calc(100% - 1.3rem);
  width: 100%;
  background: #f3f3f3;
  background: var(--color-background-light);
  box-shadow: 0px 1px 5px rgb(85, 85, 85);
  box-shadow: 0px 1px 5px var(--color-shadow);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.header-title {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 2rem;
  font-weight: bolder;
}

.header-mast .cart-wrapper {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  right: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 50%;
  margin: auto;
}

.header-mast .cart-wrapper button {
  margin: 0px 10px;
}

.header-mast .cart-wrapper .user {
  background: #2ecc71;
  background: var(--color-primary);
  color: #f8f8f8;
  color: var(--color-text-light);
  border: none;
  outline-color: #0f5e2f;
  outline-color: var(--color-tertiary);
  cursor: pointer;
  height: 100%;
  padding: 0px 15px;
}

.header-mast .cart-wrapper .cart {
  height: 100%;
  padding: 0px 10px;
  background: #2ecc71;
  background: var(--color-primary);
  color: #f8f8f8;
  color: var(--color-text-light);
  border: none;
  outline-color: #0f5e2f;
  outline-color: var(--color-tertiary);
  cursor: pointer;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

/* Aside */
.aside {
  height: 100vh;
  width: 220px;
  position: fixed;
  top: 0px;
  left: 0px;
  background-color: #2ecc71;
  background-color: var(--color-primary);
  box-shadow: 0.1px 0px 8px #0f5e2f;
  box-shadow: 0.1px 0px 8px var(--color-tertiary);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  color: #f8f8f8;
  color: var(--color-text-light);
  padding: 10px;
  box-sizing: border-box;
}

.aside-title {
  height: 3rem;
  height: var(--menu-item-height);
  border-bottom: 1px solid #f8f8f8;
  border-bottom: 1px solid var(--color-text-light);
  margin-bottom: 10px;
}

.menu-items {
  height: 100%;
  width: 100%;
}

.menu-items ul {
  list-style: none;
}

.menu-items .menu-item {
  height: 3rem;
  height: var(--menu-item-height);
  line-height: 3rem;
  line-height: var(--menu-item-height);
  display: flex;
  flex-direction: row;
  align-items: center;
}

.menu-items .menu-item a {
  color: #f8f8f8;
  color: var(--color-text-light);
  text-decoration: none;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  padding-left: 10px;
  align-items: center;
}

.menu-items .menu-item .material-icons {
  line-height: 3rem !important;
  line-height: var(--menu-item-height) !important;
  margin-right: 5px;
}

.menu-items .menu-item:hover {
  background: #27ae60;
  background: var(--color-secondary);
  cursor: pointer;
}

/* Body */
.body {
  scroll-behavior: smooth;
  -webkit-transition-duration: 0.2s;
          transition-duration: 0.2s;
}

.body-mast {
  height: calc(540px - 7rem);
  width: 100%;
  background: url('https://images.unsplash.com/photo-1484554918040-b5453ec67ccc?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=3275106e7ca706619d17629d4825f073&auto=format&fit=crop&w=1050&q=80') rgba(0, 0, 0, 0.363);
  background-blend-mode: overlay;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: scroll;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #f8f8f8;
  color: var(--color-text-light);
  text-align: center;
  font-family: 'Montserrat';
}

.shop-now-wrapper button {
  padding: 1em;
  border: none;
  background: #2ecc71;
  background: var(--color-primary);
  color: #f8f8f8;
  color: var(--color-text-light);
  outline-color: #0f5e2f;
  outline-color: var(--color-tertiary);
  margin: 1em;
}

.section {
  height: 420px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.section-title {
  width: 100%;
  text-align: center;
  position: relative;
}

.section-title::before {
  position: absolute;
  top: 50%;
  -webkit-transform: translateX(-50px);
          transform: translateX(-50px);
  content: '';
  display: inline-block;
  height: 2px;
  width: 40px;
  background: #27ae60;
  background: var(--color-secondary);
}

.section-title::after {
  position: absolute;
  top: 50%;
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
  content: '';
  display: inline-block;
  height: 2px;
  width: 40px;
  background: #27ae60;
  background: var(--color-secondary);
}

.section-content {
  height: 350px;
  width: 100%;
  display: flex;
  flex-direction: row;
  margin: 0px 20px;
}

.section-content ul {
  width: 100%;
  list-style: none;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
  margin: 10px;
}

.section-content-item {
  height: 320px;
  width: 190px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.section-content-item img {
  height: 290px;
}

.banner {
  height: 300px;
  width: 100%;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-blend-mode: overlay;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #f8f8f8;
  color: var(--color-text-light);
}

.button-wrapper button {
  padding: 1em;
  border: none;
  background: #2ecc71;
  background: var(--color-primary);
  color: #f8f8f8;
  color: var(--color-text-light);
  outline-color: #0f5e2f;
  outline-color: var(--color-tertiary);
  margin: 1em;
}

.review {
  height: 300px;
  width: 100%;
  background: url('https://i.imgur.com/RjAzkIF.jpg');
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.textarea-wrapper {
  height: 80%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.textarea-wrapper textarea {
  height: calc(100% - 10px);
  width: calc(100% - 30px);
  padding: 15px;
  font-family: 'Nunito', sans-serif;
  letter-spacing: 0.08em;
  background: #f8f8f8;
  background: var(--color-text-light);
  background-blend-mode: overlay;
  background-size: cover;
  background-position: center;
  color: #020202;
  color: var(--color-text);
  text-decoration: none;
  resize: none;
  margin: 0px 15px;
  box-sizing: border-box;
}

.review .button-wrapper {
  flex-basis: 20%;
}

.review .section-title {
  margin: 10px 0px;
  color: #f8f8f8;
  color: var(--color-text-light);
}

/* Footer */
.footer {
  height: 200px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  box-sizing: content-box;
  margin-top: 10px;
}

.footer-body {
  height: calc(100% - 1.4rem);
  width: 100%;
  background: #27ae60;
  background: var(--color-secondary);
  color: #f8f8f8;
  color: var(--color-text-light);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
}

.footer-info {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}

.footer-info ul {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}

.footer-body span {
  font-weight: bolder;
}

.footer-more {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}

.footer-more ul {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}

.copyright {
  height: 1.4rem;
}
